import React , {useState} from 'react';
import AddUser from './components/Users/AddUser';
import UsersList from './components/Users/UsersList';

function App() {

  const [userList, setUserList] = useState([]);

  const submithandler = (uTask, uDate) => {
    setUserList(
      (userList) => {
        return [{ id:Math.random(), task:uTask, deadline:uDate }, ...userList]
      }
    );
  };

  const ondeletehandler = task_id => {
    console.log(task_id);
    setUserList(
      (current) => {
        const updatedTask = current.filter(task => task.id !== task_id);
        return updatedTask;
      });
  }

  return (
    <div>
      <AddUser onSubmit = {submithandler} ></AddUser>
      <UsersList users = {userList} onDelete = {ondeletehandler}></UsersList>
    </div>
  );
}

export default App;
